
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Popup from 'vux/src/components/popup/index.vue'

import { AjaxSilentHttp, AjaxFullHttp } from '../../../js/ajaxHttp'
import { APP_BUILD, PLATFORM } from '../../../config.js'

export default {
name: 'set',
components: {
XButton,
Group,
Cell,
Popup
},
data() {
return {
itemlist: [
/*{
title: '提交意见',
path: 'list/' + 'default',
url: require('../../../assets/my_opinion.png')
}, */
{
title: '版本更新',
type: 'update',
url: require('../../../assets/my_update.png')
}, {
title: '关于',
type: 'about',
url: require('../../../assets/my_about.png')
}
],
updateShow: false,
updateContent: '',
updateUrl: '',
}
},
mounted() {

},
methods: {
judgeDisplay(type) {
if (type === 'update') {
if (this.isAndroid() && PLATFORM !== 'web') {
return true;
}
return false;
}
return true;
},
isAndroid() {
var u = navigator.userAgent;
return u.indexOf('Android') > -1 || u.indexOf('Adr') > -1;
},
goOut() {
AjaxSilentHttp('POST', 'm=user&a=logout', {}, this, function(res, wself) {});

this.$store.dispatch('setUserInfo', ''); 
this.$store.commit('updateDynamicList', {});
this.$store.commit('updateCrmDetail', '');
this.$store.commit('updateNewsDetail', []);
this.$store.commit('updateCrmListInfo', {
type: '',
list: [],
p: 1,
scroll: 0,
add: false, //是否有添加权限
loadingState: 0, //0未完成 2 完成加载 0: stop, 1: loading, 2: stopping loading
});
this.$store.commit('updateLogListInfo', {
listItem: [],
listFile: [],
showFileList: false,
p: 1,
scroll: 0,
loadingState: 0, //0未完成 2 完成加载 0: stop, 1: loading, 2: stopping loading
refresh: false, //添加成功后需要刷新数组
});
this.$store.commit('updateAddressBook', []);
window.sessionStorage.removeItem("user"); 
window.localStorage.removeItem("user");
window.localStorage.removeItem("system_name");
window.localStorage.removeItem("wkurl");
window.localStorage.removeItem("wkmoresort");
this.$vux.toast.text('退出成功', 'top');
this.$router.go(-1);

if (PLATFORM !== 'web' && window.xgpushwindow.xgpush) {
window.xgpush.unregisterPush();
}


},
enterType(type) {
if (type === 'about') {
this.$router.push({
name: 'about'
});
} else if (type === 'update') {

AjaxFullHttp('POST', 'http://www.crm.com/index.php?m=upgrade&a=checkMobileVersion', { params: JSON.stringify({ "channel": "CRM2", "type": this.isAndroid() ? "1" : "2", "versioncode": APP_BUILD }) }, this, function(res, wself, success) {
if (success) {
if (typeof res.data === 'object') {
wself.updateContent = res.data.updatemessage;
wself.updateUrl = res.data.url;
wself.updateShow = true;
} else {
wself.$vux.toast.text('当前是最新版本', 'top');
}
}
});
}
},
goUpdate() {
window.open(this.updateUrl, '_system');
}
}
}




